import { Component, Inject } from "@angular/core";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";

export interface DialogData {
  title: string;
}

@Component({
  selector: "app-modalbefore",
  templateUrl: "./modalbefore.component.html",
  styleUrls: ["./modalbefore.component.css"],
})
export class ModalbeforeComponent {
  constructor(
    public dialogRef: MatDialogRef<ModalbeforeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
}
