import { Component, Inject } from "@angular/core";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";
import { ModalbeforeComponent } from "../gameday/modalbefore/modalbefore.component";

@Component({
  selector: "app-gameday",
  templateUrl: "./gameday.component.html",
  styleUrls: [
    "./gameday.component.css",
    "../../assets/css/bootstrap.min.css",
    "../../assets/css/style.css",
    "../../assets/css/flaticon.css",
    "../../assets/css/themify-icons.css",
  ],
})
export class GamedayComponent {
  title: string;

  constructor(public dialog: MatDialog) {}

  openDialogBefore(): void {
    const dialogRef = this.dialog.open(ModalbeforeComponent, {
      width: "800px",
      data: { title: this.title },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }
}
