import { NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { FormsModule } from "@angular/forms";
import { RouterModule, Routes } from "@angular/router";

import { AppComponent } from "./app.component";
import { HeaderComponent } from "./navigate/header/header.component";
import { FooterComponent } from "./navigate/footer/footer.component";

import { MaintenanceComponent } from "./maintenance/maintenance.component";

import { HomeComponent } from "./home/home.component";
import { PeopleComponent } from "./people/people.component";
import { GamedayComponent } from "./gameday/gameday.component";
import { ModalbeforeComponent } from "./gameday/modalbefore/modalbefore.component";

import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { MatDialogModule } from "@angular/material/dialog";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";

const appRoutes: Routes = [
  { path: "", component: HomeComponent },
  { path: "people", component: PeopleComponent },
  { path: "gameday", component: GamedayComponent },
  { path: "maintenance", component: MaintenanceComponent },
];

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    ),
    BrowserAnimationsModule,
    MatDialogModule,
  ],
  bootstrap: [AppComponent],
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    MaintenanceComponent,
    HomeComponent,
    PeopleComponent,
    GamedayComponent,
    ModalbeforeComponent,
  ],
})
export class AppModule {}
